const navigation = `
fragment NavigationItem on NavigationGroupItem {
	title
	url
}

query ($filter: NavigationFilterInput) {
	navigation(filter: $filter) {
		groupId
		title
		updatedAt
		updatedBy
		items {
			...NavigationItem
			items {
				...NavigationItem
				items {
					...NavigationItem
					items {
						...NavigationItem
						items {
							...NavigationItem
						}
					}
				}
			}
		}
	}
}

`;

export default navigation;
